@import "styles/colors";
@import "styles/fonts";

.worldmap {
	margin: 24px 0 0;
	flex: 1 1 auto;
	min-height: 1px;
	max-height: 256px;
	@media (max-width: 480px) {
		flex: 0 0 auto;
		max-width: initial;
		margin: 8px 0;
	}

	.mapframe {
		background-color: transparent;
		border: none;
		width: 100%;
		height: 100%;
	}

	.mapwrapper {
		background-position: center;
		border-radius: 8px;
		height: 100%;
		overflow: hidden;
		width: 100%;
	}
}
.mapwrapper {
	background-image: url(./assets/background.png);
}

